.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.bodyContainer {
  width:75%;
  min-width: 400px;
  margin:0 auto;
}

.bpmRangeContainer {
  width: 80%;
  margin: 30px auto;
  height: 60px;
}

.input-range__slider {
  background: #3f51b5;
  border: 1px solid #3f51b5;
}

.input-range__track--active {
  background: #3f51b5;
}


.rightIcon {
  padding-left: 10px;
}

.create_playlist {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-wrap:wrap;
}

.create_playlist > button {
  margin: 0 15px;
}

.get_token {
  margin-top: 15px !important;
}

h4 {
  margin-top: 0px;
  font-size: 20px;
}

.bpm_label {
  text-align: left;
  display: block;
  margin: 15px;
}

.open_playlist {
  margin-top:10px;
}